
aside {
    display: none;

    // Copied from the theme's `.note` styles:
    background-color: rgba($theme-col, 12%);
    border-left: 2px solid $theme-col;
    margin: 1em 0;
    padding: 0 1em 0 calc(1em - 2px);
    padding-top: 1em;
    padding-bottom: 1em;
}

aside.visible {
    display: flow-root;
}

span[data-aside] {
    color: green;
    cursor: pointer;
}

span[data-aside]:hover {
    text-decoration: underline;
    text-decoration-style: dotted;
}

div.note {
    // Copied from the theme's `p.note` style
    padding-top: 1em;
    padding-bottom: 1em;
}

code {
    white-space: nowrap;
}

code.highlighter-rouge,
.highlight pre {
    // Make it stand out a bit better than in the base template
    border-radius: 6px;
    border: 1px solid #e3e3e3;
}

code.highlighter-rouge {
    padding-bottom: 0.1em;
    position: relative;
}

pre > code {
    white-space: pre;
}

object[type="image/svg+xml"] {
    // Same settings as for `img` in the base template
    max-width: 100%;
    display: block;
    margin: 0.4em auto;
}

object[type="image/svg+xml"].inline-block {
    display: inline-block;
}

.centering-container {
    display: flex;
    align-items: center;
    justify-content: center;
}

main li {
     padding-bottom: 9px;
}

main li > ul,
main li > ol {
     padding-top: 9px;
}

main.fullscreen-layout {
    max-width: none;
}

a.no-underline {
    text-decoration: none;
}

a.no-visited-style,
a.no-visited-style:visited {
    color: blue;
}

html.dark-theme object[type="image/svg+xml"] {
    filter: grayscale(0.75) invert(1) contrast(0.84);
}
